___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: hsl(0, 31%, 53%);
  --primary-color-darker: hsl(160, 25%, 12%);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
}

.container {
  max-width: 640px;
  margin: 50px auto;
  background: #fff;
  padding: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 1.5em;
  line-height: 1.5em;
  border-radius: 10px;
}

form input,
form label,
form button {
  margin-bottom: 10px;
  display: block;
  width: 100%;
}

form input {
  font-size: 24px;
  height: 50px;
  padding: 0 20px;
}

form input:focus {
  outline: 1px solid var(--primary-color);
}

form button {
  border: none;
  background: var(--primary-color);
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  height: 50px;
  cursor: pointer;
  margin-top: 35px;
}

form button:hover {
  background: var(--primary-color-darker);
}

.password {
  font-size: 1.5em;
  margin-bottom: 40px;
  margin-top: 40px;
}

input[type="checkbox"] {
  width: 25px;
  height: 25px;
}

button {
  display: block;
  margin: 40px 0;
  font-size: 1em;
}

.password-length {
  font-size: large;
